import React from "react";
import { Grid } from 'material-ui';

export const LayoutEngine = (getWdigetById, layouts) => {
      return layouts.map((layout, i) => {
            if (layout.flex === 'row') {
                  return <Grid key={i + 'layout'} container style={{paddingBottom: 20}} {...layout.props}>
                        {layout.items.map((item, j) => 
                              <Grid key={i + 'layout' + j} item style={{display: 'grid'}} md={item.props?.size12 || true}>
                                    {getWdigetById(item.mfId)}
                              </Grid>)}
                  </Grid>
            } else if (layout.flex === 'column'){
                  return <Grid key={i + 'layout'} container direction="column" style={{paddingBottom: 20}} {...layout.props}>
                        {layout.items.map(item => 
                              <Grid key={i + 'layout' + j} item style={{display: 'grid'}} md={item.props?.size12 || true}>
                                    {getWdigetById(item.mfId)}
                              </Grid>)}
                  </Grid>      
            }
      })
}

export const hasAccess = (perms, str) => {
      const perm = perms[str]
      return !perm.includes('dummy')
}