import { hasAccess, LayoutEngine } from  "./layout.engine";

// Start: Microfrontend import
import { Widget as Widget1 } from  "@ab/w-balance-card";
import { Widget as Widget2 } from  "@ab/w-card-spend";
// End: Microfrontend import

const useWdigetById = ({ permissions, profile }) => mfId => {
      let widget = <></>;
      switch (mfId) {
      // Start: Microfrontend named export
            case '@ab/w-balance-card':
                  widget = <Widget1 profile={profile} />;
                  break;
            case '@ab/w-card-spend':
                  widget = <Widget2 profile={profile} />;
                  break;
      // End: Microfrontend named export
      }
      return hasAccess(permissions, mfId) ? widget : <></>
};

const layouts = [
      {
            flex: 'row',
            props: {
                  justifyContent: 'space-between', spacing: 2,      
            },
            items: [
                  {
                        mfId: '@ab/w-balance-card',
                        props: { }
                  },
                  {
                        mfId: '@ab/w-balance-card',
                        props: { }
                  },
                  {
                        mfId: '@ab/w-balance-card',
                        props: { }
                  },
                  {
                        mfId: '@ab/w-balance-card',
                        props: { }
                  },
                  {
                        mfId: '@ab/w-balance-card',
                        props: { }
                  },
            ]
      },
      {
            flex: 'row',
            props: {
                  justifyContent: 'space-between', spacing: 2, alignItems: 'stretch'  
            },
            items: [
                  {
                        mfId: '@ab/w-card-spend',
                        props: { }
                  },
                  {
                        mfId: '@ab/w-card-spend',
                        props: { size12: 3 }
                  },
                  {
                        mfId: '@ab/w-card-spend',
                        props: { }
                  },
            ]
      },
]

export default function Root(props) {
      return LayoutEngine(useWdigetById(props), layouts)
}
